




/* eslint-disable no-shadow */
import {
  defineComponent, ref, onMounted, getCurrentInstance,
} from '@vue/composition-api';
import {
  PaperScope,
  Color,
} from 'paper';
import Bezier from '@/utils/paper-tools/Bezier';


type DorE = Document | HTMLElement;

type EventMap<T extends DorE> =
  (T extends Document ? DocumentEventMap : HTMLElementEventMap);

function on<T extends DorE, EM extends EventMap<T>, TP extends Extract<keyof EM, string>>(
  target: T,
  type: TP,
  handler: (e: EM[TP]) => void,
  options?: boolean | EventListenerOptions,
) {
  const _handler = handler as unknown as EventListener;
  target.addEventListener(type, _handler, options);
  return () => {
    target.removeEventListener(type, _handler, options);
  };
}


export default defineComponent({
  setup() {
    const paper = new PaperScope();
    const {
      Layer,
    } = paper;


    const canvas = ref<HTMLCanvasElement>(null);
    const that = getCurrentInstance();
    paper.install(that);


    onMounted(() => {
      const strokeColor = new Color('black');
      const strokeWidth = 4;
      paper.setup(canvas.value!);

      const layer = new Layer();
      layer.activate();

      const bezier = new Bezier(paper, { strokeColor, strokeWidth });
      bezier.activate();
      console.log(paper.tool);
    });
    return {
      canvas,
    };
  },
});
